#Portfolio {
  background-color: #eeeeee;
}
.grid {
  width: 100%;
  max-width: 980px;
  margin-top: 16px;
}
.column {
  width: 100%;
  float: left;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.proj-container {
  padding: 16px 0 64px 0;
  max-width: 385px;
  text-decoration: none;
  color: #000000;
}
.proj-img {
  width: 100%;
}
.proj-title {
  font-size: 28px;
  color: #333333;
}
.description {
  color: #595959;
}
.circle-icon {
  height: 8px;
  width: 8px;
  margin-right: 8px;
}
.technologies {
  color: #595959;
  font-weight: 700;
}
@media only screen and (min-width: 780px) {
  .column {
    width: 50%;
  }
  .proj-container {
    padding-left: 24px;
    padding-right: 24px;
  }
}
@media only screen and (min-width: 1300px) {
  .column {
    width: 33.33%;
  }
}



