#Home {
  height: 100vh;
  background: url("../../img/home/main.jpg") 50% 35%/cover;
}
.overlay {
  height: 100vh;
  background-color: rgba(48, 48, 48, 0.7);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: #ffffff;
}
.title {
  margin: 32px 16px;
  text-align: center;
}
.title h1 {
  margin: 0;
  font-size: 34px;
  font-weight: 600;
}
.title h2 {
  margin: 0;
  font-size: 38px;
  font-weight: 400;
}
.home-name {
  color: #ff4d4d;
  font-size: 36px;
}
.button-transparent {
  width: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  background: none;
  color: #ffffff;
  border: solid 2px #ffffff;
  -webkit-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.button-transparent:hover {
  width: 160px;
  background: #ff4d4d;
  border-color: #ff4d4d;
}



